<template>
  <div>
    <div class="memberTopBg">
      <div class="box">
        <router-link class="memberLogo" to="/"></router-link>
        <ul class="memberMenu">
          <li>
            <router-link v-bind:class="{ active: index==0 }" to="member">个人主页</router-link>
          </li>
          <li>
            <router-link v-bind:class="{ active: index==1 }" to="shop">礼品商城</router-link>
          </li>
          <li>
            <router-link v-bind:class="{ active: index==2 }" to="buy">立即采购</router-link>
          </li>
          <li>
            <router-link v-bind:class="{ active: index==3 }" to="order">订单管理</router-link>
          </li>
          <li>
            <router-link v-bind:class="{ active: index==4 }" to="check">查件/补发</router-link>
          </li>
        </ul>
        <div class="memberRight">
          账户余额： {{mytotal/100}}元
          <el-button @click="$router.push({path:'/recharge'})" size="small" class="chongzhi">充值</el-button>
          <el-dropdown>
            <span class="el-dropdown-link">
              Hi~{{member.account}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <div @click="$router.push({path:'/cost'})">物流成本</div>
              </el-dropdown-item>
              <el-dropdown-item>
                <div @click="$router.push({path:'/capitalDetail'})">资金明细</div>
              </el-dropdown-item>
              <el-dropdown-item>
                <div @click="editMmDiv=true">修改密码</div>
              </el-dropdown-item>
              <el-dropdown-item divided>
                <div @click="share">分享站点</div>
              </el-dropdown-item>
              <el-dropdown-item divided>
                <div @click="out">退出系统</div>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <el-dialog title="修改密码" :visible.sync="editMmDiv" width="30%">
      <el-form label-width="120px">
        <!-- <el-form-item label="手机号">
    <el-input></el-input>
  </el-form-item>
  <el-form-item label="验证码">
     <el-input style="width:50%"></el-input>
     <el-button type="primary">获取验证码</el-button>
        </el-form-item>-->
        <el-form-item label="旧密码">
          <el-input v-model="oldPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="新密码">
          <el-input v-model="password" show-password></el-input>
        </el-form-item>
      </el-form>
      <div class="mar cen">
        <el-button @click="editMmDiv = false">取 消</el-button>
        <el-button type="primary" @click="editMmDiv = false;editPassword()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { hex_md5 } from "../../utils/md5";
export default {
  props: {
    index: Number,
    required: true
  },
  data() {
    return {
      mytotal: 0,
      password: "",
      oldPassword: "",
      editMmDiv: false,
      member: ""
    };
  },
  created() {
    this.getInviteCode();
  },
  mounted() {
    let that = this;
    this.axios
      .get(
        // process.env.API_HOST+"wallet/myTotal",
        "/giftmall/wallet/myTotal",
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            // "domain": "taobao.tianyalei.com",
            domain: this.url,
            Authorization: sessionStorage.getItem("token")
          }
        }
      )
      .then(function(response) {
        if (response.data.code == 200) {
          that.mytotal = response.data.data;
        }
      });
    this.axios
      .get(
        // process.env.API_HOST+"user/me",
        "/giftmall/user/me",
        {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            // "domain": "taobao.tianyalei.com",
            domain: this.url,
            Authorization: sessionStorage.getItem("token")
          }
        }
      )
      .then(function(response) {
        if (response.data.code == 200) {
          that.member = response.data.data;
          that.$emit("member", that.member);
        }
      });
  },
  methods: {
    getInviteCode() {
      this.axios.get("/giftmall/user/me").then(res => {
        const { data } = res.data;
        this.inviteCode = data.inviteCode;
        console.log(data);
      });
    },
    share() {
      const route = this.$route;
      console.log(route);
      const link = location.origin + "/#/" + "?inviteCode=" + this.inviteCode;
      const linkDom = `<input id="share" class="el-input" type="text" disabled value=${link} />`;
      this.$alert(linkDom, "站点分享", {
        dangerouslyUseHTMLString: true,
        confirmButtonText: "复制",
        callback: action => {
          const mesDom = document.getElementById("share");
          mesDom.select(); // 选择对象
          document.execCommand("Copy"); // 执行浏览器复制命令
          this.$message({
            type: "info",
            message: `复制成功`
          });
        }
      });
    },
    editPassword() {
      let that = this;
      // this.axios.put(process.env.API_HOST+'user/password',
      this.axios
        .put(
          "/giftmall/user/password",
          this.qs.stringify({
            oldPassword: hex_md5(this.oldPassword),
            password: hex_md5(this.password)
          }),
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              _domain: this.url,
              get domain() {
                return this["_domain"];
              },
              set domain(value) {
                this["_domain"] = value;
              },
              Authorization: sessionStorage.getItem("token")
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
            that.$message({
              message: "修改成功",
              type: "success"
            });
            that.$router.push({
              path: "/login"
            });
          } else {
            that.$message({
              message: response.data.message,
              type: "warning"
            });
          }
        });
    },
    out() {
      sessionStorage.removeItem("token");
      this.$router.push({
        path: "/login"
      });
    }
  }
};
</script>

<style scoped>
.memberTopBg {
  background: #ec3440;
  height: 50px;
  line-height: 50px;
  color: #fff;
  font-size: 16px;
}
.memberLogo {
  display: block;
  float: left;
  height: 50px;
  width: 150px;
  background: url(../../../static/logoMember.png) no-repeat left center;
}
.memberMenu {
  float: left;
  width: 600px;
}
.memberMenu li {
  font-size: 18px;
  float: left;
  width: 120px;
  text-align: center;
}
.memberMenu li a {
  color: #fff;
  display: block;
}
.memberMenu li a.active,
.memberMenu li a:hover {
  background: #b51f29;
}
.memberRight {
  float: right;
  width: 400px;
}
.chongzhi {
  font-size: 14px;
  border: 0;
  color: #ec3440;
  margin-left: 10px;
  margin-right: 10px;
}
.el-dropdown-link {
  cursor: pointer;
  color: #fff;
}
.el-icon-arrow-down {
  font-size: 14px;
}
</style>



